@value font-lato from '../variables.module.css';

.wrapper {
    width: 250px;
    background-color: #fff;
    margin: auto;
    margin-top: 10%;
    border-radius: 2%;
    font-family: font-lato;
}

.imgWrp {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.img {
    margin-top: 30px;
    border-radius: 50%;
    width: 100px;
}

.name {
    font-weight: 700;
    font-size: 18px;
    margin: 0;
    margin-top: 26px;
}

.greyTitle {
    font-weight: 400;
    color: #768696;
    margin: 0;
    margin-top: 12px;
}

.list {
    display: flex;
    list-style: none;
    background-color: #F3F6F9;
    padding: 0;
    margin: 0;
    margin-top: 30px;
}

.stats {
    display: flex;
    flex-direction: column;
    text-align: center;
    border: 1px solid #E3E9F0;
     /* flex-grow: 1; */
    width: 86px;
    font-size: 12px;
    font-weight: 400;
    color: #768696;
    padding-top: 18px;
    padding-bottom: 20px;
}

.span {
    color: #1f3349;
    font-weight: 900;
    font-size: 14px;
    padding-top: 5px;
}

