@value font-muli from '../variables.module.css';

.table {
    table-layout: fixed;
    width: 874px;
    font-family: font-muli;
    text-align: center;
    background-color: #fff;
    box-shadow: 3px 3px 9px -2px rgba(120,120,120,0.66);
    margin: 0 auto;
    margin-bottom: 30px;
    border-spacing: 0;
}

.thead {
    padding: 14px 0 14px 0;
    background-color: #00BCD4;
    border-right: 1px solid #E0E6E7;
    border-spacing: 0;
    font-size: 16px;
    color: #fff;
}


.tbody {
    color: #5c6975;
    padding: 14px 0 14px 0;
    border-right: 1px solid #E0E6E7;
}

.tr:nth-child(2n) {background: #ECF2F3;}
